<template>
    <van-tabbar
        v-model="active">
        <van-tabbar-item 
        v-for="(foot,i) in foots" 
        :key="i" 
        :info="i==2?cars.carNum:''"
       
        :icon="foot.icon">{{foot.txt}}</van-tabbar-item>
    </van-tabbar>
</template>


<script>
import {mapState,mapMutations} from "vuex";
export default {
    computed:{
        ...mapState([
            'cars',
            
        ]),
        active:{
            get(){
                return this.$store.state.active
            },
            set(newVal){
                 this.changeActive(newVal);
            }
        }
    },
    data(){
        return {
            foots:[
                {txt:"首页",path:"/index/home",name:"home",icon:"wap-home"},
                {txt:"分类",path:"/index/classify",name:"classify",icon:"qr-invalid"},
                {txt:"购物车",path:"/index/cart",name:"cart",icon:"shopping-cart-o"},
                {txt:"我",path:"/index/mine",name:"mine",icon:"friends-o"}
            ],
            // active:0
        }
    },
    watch:{
        active:function(newVal){
            console.log(newVal);
            this.$router.push({name:this.foots[newVal].name})
        }
    },
    methods:{
        ...mapMutations(['changeActive'])
    },
    mounted(){
        let name = this.$route.name;
        this.foots.forEach((item,index)=>{
            if(item.name == name){
                // this.active = index;
                this.changeActive(index);
            }
        })
    }
}
</script>



